@import "styles/variables.scss";

.banner {
    display: grid;
    grid-template-columns: 4fr 4fr 96px;
    grid-template-rows: 1fr 1fr 96px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    padding: 24px;
    position: relative;
    height: 30vw;
    max-height: 432px;
}

.banner > .info > h1 {
    font-size: 20px;
    margin-left: 30px;
}

.banner > .info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.banner :first-child {
    grid-row-start: 1;
    grid-row-end: 4;
}

.banner :nth-child(2) {
    grid-row-start: 1;
    grid-row-end: 4;
}

.banner > :last-child {
    grid-row-start: 3;
    grid-row-end: 4;
    bottom: -15px;
    right: -20px;
}

.data {
    position: relative;
    top: 5px;
    left: 30px;
}

.data > p:first-child {
    font-size: 20px;
    margin-bottom: 40px;
}

.data > a {
    text-decoration: none;
}

.data > a > p:last-child {
    font-size: 46px;
    max-width: 510px;
    line-height: 110%;
    letter-spacing: -0.03em;
}

@media screen and (min-width: 1681px) {
    .banner {
        height: 500px;
    }
}

@media #{$mobile} {
    .banner {
        grid-template-rows: 0.5fr 5fr 1fr;
        height: 100vw;
        max-height: 150vw;
        padding: 16px;
    }
    .banner img:first-child {
        grid-row-start: 2;
        grid-row-end: 3;
        grid-column-start: 1;
        grid-column-end: 4;
        aspect-ratio: 334/254;
        height: auto!important;
    }
    
    .banner :nth-child(2) {
        flex-direction: row;
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 1;
        grid-column-end: 4;
    }

    .banner :nth-child(3) {
        grid-row-start: 3;
        grid-row-end: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        margin-top: 100px;
        text-decoration: none;
    }

    .banner :nth-child(3) > p {
        font-size: 32px;
    }
    
    .banner > :last-child {
        grid-row-start: 3;
        grid-row-end: 4;
        grid-column-start: 3;
        grid-column-end: 4;
        position: absolute!important;
        bottom: -10px;
        right: -15px;
        min-width: 60px;
        min-height: 60px;

    }
    .banner > .info > h1 {
        font-size: 16px;
        margin-left: 0;
        margin-bottom: 16px;
    }
    .data > p:first-child {
        font-size: 16px;
        margin-bottom: 16px;
    }
    .data {
        position: static;
    }
}

@media #{$small} {
    .banner {
        grid-template-rows: 0.5fr 5fr 2fr;
        height: 120vw;
    }
}