@import "styles/variables.scss";

.orderInfo {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.block {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.block > p:first-child {
    font-size: 20px;
    margin-bottom: 24px;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 100%;
}

.block > p:last-child {
    font-size: 80px;
}

@media #{$tablet} {
    .orderInfo {
        flex-direction: column;
    }
    .block:not(:last-child) {
        margin-bottom: 40px;
    }
    .block > p:first-child {
        font-size: 16px;
        margin-bottom: 16px;
        text-align: center;
    }
    .block > p:last-child {
        font-size: 56px;
        line-height: 100%;
    }
}

@media #{$small} {
    .block > p:last-child {
        font-weight: 600;
    }
}