@import "styles/variables.scss";

.categories {
    display: flex;
    overflow-x: scroll;
    transform: translateX(-40px);
    width: calc(100vw - 55px);
    padding-left: 40px;
}

.categories {
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.categories::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.categories > div:not(:last-child) {
    margin-right: 15px;
}

.categories > div:last-child {
    margin-right: 40px;
}

.button {
    margin: 60px 0 0;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}

@media screen and (min-width: 1681px) {
    .categories {
        width: calc(100% - 25px);
        transform: translate(0);
        padding-left: 0;
    }
}

@media #{$tablet} {
    .categoriesCol {
        margin-bottom: 80px;
    }
    button.button {
        width: 100%;
        margin-top: 32px;
    }

    .categories {
        transform: translateX(-12px);
        width: calc(100vw - 12px);
        padding-left: 12px;
    }
    .categories > div:last-child {
        margin-right: 12px;
    }
}