@import 'styles/variables.scss';

.email {
    display: flex;
    flex-direction: column;
    background-color: $darkBlue;
    padding: 40px;
    width: calc(100vw - 80px);
    transform: translateX(-40px);
}

.email > p {
    color: $beige;
    margin-bottom: 100px;
    max-width: 370px;
}

.input > input {
    width: 100%;
    height: 95px;
    border: none;
    background-color: transparent;
    color: $beige;
    font-size: 80px;
    padding-bottom: 10px;
}

.input > input:focus-visible {
    outline: none;
}

.input > input::placeholder {
    color: $beige;
    font-size: 80px;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
}

.input > input.isOk::placeholder {
    opacity: 0.3;
}

.input {
    display: flex;
    width: 100%;
    border-bottom: 1px solid #F3F3EA;
}

.input :first-child {
    z-index: 0;
}

.input :last-child {
    top: 0;
    cursor: pointer;
    z-index: 1;
}

@media screen and (min-width: 1681px) {
    .email {
        width: 100%;
    }
}

@media #{$tablet} {
    .email {
        padding: 16px 16px 24px;
        width: calc(100vw - 32px);
        transform: translateX(-12px);
    }
    .email > p {
        font-size: 12px;
        margin-bottom: 64px;
    }
    .input {
        justify-content: flex-end;
        margin: 0;
        height: 80px;
        width: 100%;
    }
    .input > input {
        font-size: 56px;
        width: calc(100% - 60px);
        position: absolute;
        left: 25px;
        height: 75px;
    }
    .input > input::placeholder {
        font-size: 56px;
    }
}

@media #{$small} {
    .input {
        align-items: flex-end;
    }
    .input *:last-child {
        top: -5px;
    }
    .input > input {
        width: calc(100% - 60px);
        padding: 0 50px 0 0;
        box-sizing: border-box;
    }
    .email > p {
        max-width: 200px;
    }
}