@import "styles/variables.scss";

.category {
    display: flex;
    flex-direction: column;
    position: relative;
    width: fit-content;
    min-width: 408px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    box-sizing: border-box;
}
.category > a {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.category > a > img {
    width: 100%!important;
    height: auto!important;
    position: relative!important;
    background-image: url('/static/img/miniLoader.svg');
    background-size: 30%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.content {
    display: flex;
    margin: 14px 16px 16px;
}
.content > p:first-child, .content .text {
    font-size: 46px;
    word-wrap: break-word;
    white-space: pre;
    max-width: 94%;
    width: min-content;
    line-height: 110%;
    letter-spacing: -0.03em;
    position: relative;
}

.content .count {
    position: absolute;
    top: -15px;
}

.content > :last-child {
    position: absolute!important;
    bottom: 18px;
    right: 16px;
}

@media #{$tablet} {
    .content {
        margin: 7px 8px 8px;
    }
    .content > p:first-child {
        font-size: 32px;
    }
    .content > :last-child {
        bottom: 8px;
    }
}

@media #{$small} {
    .category {
        min-width: 70vw;
    }
}