@import "styles/variables.scss";

.banner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(35, 35, 35, 0.3);
    width: 85%;
    margin: auto;
}

.banner > :last-child {
    height: 100%!important;
    position: relative!important;
}

.content {
    margin: 50px 25px 50px 50px;
}

.content > h1 {
    font-size: 20px;
    margin-bottom: 40px;
}

.content > h2 {
    font-size: 80px;
    margin: 0 0 16px;
}

.content > p {
    max-width: 635px;
    margin-bottom: 32px;
}

@media #{$mobile} {
    .banner {
        flex-direction: column;
        align-items: inherit;
        width: 100%;
    }
    .content {
        margin: 24px;
    }
    .content > h1 {
        font-size: 16px;
    }
    .content > h2 {
        font-size: 56px;
    }
    .content > p {
        margin-bottom: 24px;
    }
    .content > button {
        width: 100%;
    }
}

@media #{$small} {
    .bannerCol {
        margin-bottom: 80px;
    }
    .content > h2 {
        font-size: 32px;
    }
    .content > button > pre {
        font-size: 12px;
    }
}