@import "styles/variables.scss";

div.content {
    padding-bottom: 270px;
}

@media #{$tablet} {
    div.content {
        padding-bottom: 460px;
    }
}

.email {
    display: flex;
    flex-direction: column;
    width: 370px;
    padding: 24px;
    padding-bottom: 40px;
    white-space: pre-wrap;
}

.email > h2 {
    color: $darkBlue;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
    font-size: 28px;
    line-height: 34px;
    margin: 0;
    margin-bottom: 16px;
}

.email > p {
    color: $darkBlue;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 40px;
}

.input > input {
    width: 100%;
    height: 16px;
    border: none;
    background-color: transparent;
    color: $darkBlue;
    font-size: 16px;
    padding-bottom: 10px;
}

.input > input:focus-visible {
    outline: none;
}

.input > input::placeholder {
    color: $placeholderGrey;
    font-size: 16px;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
}

.input > input.isOk::placeholder {
    opacity: 0.3;
}

.input > span {
    font-size: 12px;
    line-height: 16px;
}

.input {
    display: flex;
    width: 100%;
    border-bottom: 1px solid $darkBlue;
}

.input :first-child {
    z-index: 0;
}

.input :last-child {
    cursor: pointer;
    z-index: 1;
}

@media #{$small} {
    .email {
        width: 310px;
        padding: 16px;
        padding-bottom: 24px;
    }

    .email > h2 {
        font-size: 22px;
        line-height: 26px;
    }
    
    .email > p {
        font-size: 12px;
        line-height: 15px;
        margin-bottom: 32px;
    }
}