@import "styles/variables.scss";

.MainBannerContainer {
    overflow: hidden;
    position: relative;
}

.arrows {
    position: absolute;
    top: 43%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
}

button.arrowLeft,
button.arrowRight {
    cursor: pointer;
    background: none;
    padding: 0 40px;
}

button.arrowLeft img,
button.arrowRight img {
    width: 48px;
    height: 48px;
    opacity: 0.8;
}

button.arrowLeftMobile,
button.arrowRightMobile {
    cursor: pointer;
    background: none;
    opacity: 0.8;
    padding: 0 24px;
}

button.arrowRightMobile img,
button.arrowRightMobile img {
    width: 48px;
    height: 48px;
    opacity: 0.8;
}

.MainBanner {
    display: flex;
    height: fit-content;
    width: fit-content;
    transition: 0.5s;
}

.MainBanner > div {
    width: 100vw;
    margin-bottom: 0px;
    padding-bottom: 0px;
}

@media #{$tablet} {
    .MainBanner {
        overflow: hidden;
    }
    .MainBanner > div {
        min-width: 100vw;
        margin-bottom: 0;
    }
}

.swiper {
    height: auto;
    margin-bottom: 0px;
    padding-bottom: 0px;
}
